import App, {Container} from 'next/app'
import NProgress from 'nprogress'
import Router from 'next/router'
import Link from 'next/link'

Router.events.on('routeChangeStart', (url) => {
  console.log(`Loading: ${url}`)
  NProgress.start()
})
Router.events.on('routeChangeComplete', () => NProgress.done())
Router.events.on('routeChangeError', () => NProgress.done())

export default class MyApp extends App {
  static async getInitialProps ({ Component, router, ctx }) {
    let pageProps = {}

    if (Component.getInitialProps) {
      pageProps = await Component.getInitialProps(ctx)
    }

    return {pageProps}
  }
  
  componentDidMount () {
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker
        .register(process.env.BACKEND_URL +'/service-worker.js')
        .then(registration => {
          console.log('service worker registration successful')
        })
        .catch(err => {
          console.warn('service worker registration failed', err.message)
        })
    }
    $("header nav a").hover(function() {
        $("header navslider").css({
          left: $(this).position().left + parseInt($(this).css("marginLeft")) + "px",
          width: $(this).width() + "px",
          opacity: ".999"
        })
      }, function() {
        $("header navslider").css("opacity", "0")
    });
  }

  render () {
    const {Component, pageProps} = this.props
    return (
      <Container>
        <header>
          <logo><text>AVN SYSTEMS</text></logo>
          <nav>
            <Link href='/' as={ process.env.BACKEND_URL + `/`}><a>Home</a></Link>
            <Link href='/about' as={ process.env.BACKEND_URL + `/about`}><a>About</a></Link>
            <Link href='/tvmaze' as={ process.env.BACKEND_URL + `/tvmaze`}><a>TV Maze</a></Link>
            <Link href='/react-md' as={ process.env.BACKEND_URL + `/react-md`}><a>React MD</a></Link>
            <Link href='/404' as={ process.env.BACKEND_URL + `/404`}><a>404</a></Link>
          </nav>
          <navtoggle><bar></bar><bar></bar><bar></bar></navtoggle>
          <navslider></navslider>
        </header>
        <style jsx>{`
          header {
            overflow: hidden;
            position: fixed;
            z-index: 10000;
            height: 90px;
            width: 100%;
          }
          header logo {
            position: absolute;
            padding: 16px 0px;
            width: 170px;
            left: 90px;
            top: 10px;
            
            background-image: url(./static/images/icon.png);
            background-repeat: no-repeat;
            background-size: contain;
            
            -webkit-filter: grayscale(100%) brightness(2);
            filter: grayscale(100%) brightness(2);
            
            -webkit-filter: url(./static/images/filters.svg#grayscale);
            filter: url(./static/images/filters.svg#grayscale);
            
            -webkit-filter: gray;
            filter: gray;
            
            -webkit-filter: grayscale(100%) brightness(2);
            -moz-filter: grayscale(100%) brightness(2);
            -ms-filter: grayscale(100%) brightness(2);
            -o-filter: grayscale(100%) brightness(2);
            
            -webkit-transition: all 150ms ease-in-out;
            -moz-transition: all 150ms ease-in-out;
            -ms-transition: all 150ms ease-in-out;
            -o-transition: all 150ms ease-in-out;
            transition: all 150ms ease-in-out;
          }
          header logo:hover {
            -webkit-filter: none;
            -moz-filter: none;
            -o-filter: none;
            filter: none;
          }
          header logo text {
            display: -webkit-inline-box;
            text-transform: uppercase;
            letter-spacing: 1px;
            position: relative;
            font-weight: bold;
            min-width: 110px;
            font-size: 14px;
            color: black;
            left: 60px;
          }
          header nav {
            margin: 0 20px;
            float: right;
          }
          header nav a {
            text-decoration: none;
            display: inline-block;
            margin: 25px 10px;
          }
          header nav a:hover {
            color: #8500FF;
          }
          header navtoggle {
            cursor: pointer;
            display: inline-block;
            position: absolute;
            width: 45px;
            left: 20px;
            top: 15px;
          }
          header navtoggle bar {
            border-bottom: 2px solid #aaa;
            margin: 7px 0;
            width: 100%;
            height: 5px;
            display: block;
          }
          header navtoggle:hover bar {
            border-bottom: 2px solid #bc73ff;
          }
          header navslider {
            border-bottom: 4px solid #8500FF;
            position: absolute;
            height: 0;
            top: 50px;
            
            -webkit-transition: all 250ms ease-in-out;
            -moz-transition: all 250ms ease-in-out;
            -ms-transition: all 250ms ease-in-out;
            -o-transition: all 250ms ease-in-out;
            transition: all 250ms ease-in-out;
          }
        `}</style>

        <Component {...pageProps} />
      </Container>
    )
  }
}